body {
    background-color: rgb(226, 241, 231);
    color: rgb(226, 241, 231);
    padding: 1rem;
    margin: 0 auto;
}

.navbar {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    width: 50%;
}


hr {
    border: 1px solid rgb(56, 116, 120);
    width: 100%;
}

img {
    border: 1px solid rgb(56, 116, 120);
    border-radius: 0.25em;
    width: 50px;
    height: 50px;
}

.txt{
    color: rgb(226, 241, 231);
}

.btn {
    background: rgb(98, 149, 132);
    border: 1px solid rgb(56, 116, 120);
    color: rgb(226, 241, 231);
    padding: 0.25em 0.5em;
    border-radius: 0.25em;
    cursor: pointer;
    outline: none;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.items-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.books-container {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    width: 100%;
}

.filters {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    width: 100%;
}

.filter{
    background: rgb(253, 177, 149);
    border: 1px solid #ff7f50;
    color: rgb(226, 241, 231);
    padding: 0.25em 0.5em;
    border-radius: 0.25em;
    cursor: pointer;
    outline: none;
}

.filter-input::placeholder{
    color: rgb(226, 241, 231);
}

.book-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: rgb(226, 241, 231);
    border: 2px solid rgb(56, 116, 120);
    padding: 1rem;
    border-radius: 8px;
    width: 200px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
